import React, {useState} from "react";
import {
    dbService,
    storageService
} from "fbase";
import { doc, deleteDoc, updateDoc }from "firebase/firestore";
import {
    deleteObject,
    ref
} from "firebase/storage";


const Jweet = ({ jweetObj, isOwner }) => {
    //        

    //      
    const [editing,setEditing] = useState(false);
    // input  text 
    const [newJweet , setNewJweet] = useState(jweetObj.text);
    const onDeleteJweet = doc(dbService , "jweets", `${jweetObj.id}`);

    //     ref 
    // jweetObj attachmentUrl   URL
    const desertRef = ref(storageService, jweetObj.attachmentUrl);

    const onDeleteClick = async() => {
        const ok = window.confirm(" ?");
        if(ok){
            try{
                await deleteDoc(onDeleteJweet);

                if(jweetObj.attachmentUrl !== ""){
                    await deleteObject(desertRef);
                }


            } catch (error){
                window.alert(" .")
            }

        }
    }

    const toggleEditing = () => setEditing((prev) => !prev);
    const onSubmit = async (event) => {
        event.preventDefault();
        await updateDoc(doc(dbService, `jweets/${jweetObj.id}`), {
            text: newJweet,
        });
        setEditing(false);  //     false 
    }
    const onChange = (event) => {
        const {
            target : {value}
        } = event;
        setNewJweet(value);
    }

    return(
        <div>
            {
                editing ?
                    (
                        <>
                            <form onSubmit={onSubmit}>
                                <input
                                    type="text"
                                    value={newJweet}
                                    onChange={onChange}
                                    placeholder="  "
                                    required
                                />
                            </form>
                            <input type="submit" value="Jweet "/>
                            <button onClick={toggleEditing}></button>
                        </>
                    )
                    :
                    (
                        <>
                            <h4>{jweetObj.text}</h4>
                            { jweetObj.attachmentUrl && <img src={jweetObj.attachmentUrl} width="50px"/> }
                            {isOwner &&
                                <>
                                    <button onClick={onDeleteClick}></button>
                                    <button onClick={toggleEditing}></button>
                                </>
                            }
                        </>
                    )
            }

        </div>
    )
}

export default Jweet;