import React, {useEffect, useState} from "react";
import {dbService} from "fbase";
import {
    collection ,
    query ,
    orderBy ,
    onSnapshot,
} from "firebase/firestore";
import Jweet from "components/Jweet";
import JweetFactory from "components/JweetFactory";


const Home = ({ userObj }) => {
    // Jweets   
    const [jweets,setJweets] = useState([]);

    /*
    //  
    const getJweets = async() => {
        //component mount - async  
        const q = query(collection(dbService, "jweets"));
        const querySnapshot = await getDocs(q);


        querySnapshot.forEach(document => {
           const jweetObject = {
               ...document.data(),
               id: document.id,
           }

           // jweets    ( jweet +  jweet)
           //set       ,       .
           setJweets(prev => [jweetObject, ...prev]);
       });
    }

     */

    useEffect(()=>{
        const q = query(
            collection(dbService, "jweets"),
            orderBy("createdAt", "desc")
        );

        onSnapshot(q, (snapshot) => {
            //  ojbect , foreach   ->   
            const jweetArr = snapshot.docs.map((document) => ({
                id: document.id,
                ...document.data(),
            }));
            setJweets(jweetArr);
        });
    },[]);


    return (
        <div>
            <JweetFactory userObj={userObj}/>
            <div>
                {jweets.map((jweet) => (
                    <Jweet
                        key={jweet.id}
                        jweetObj={jweet}
                        isOwner={jweet.creatorId === userObj.uid}
                    />
                ))}
            </div>
        </div>
    )
}

export default Home;