import React, {useState} from "react";
import {getDownloadURL, ref, uploadString} from "@firebase/storage";
import {dbService, storageService} from "../fbase";
import {v4} from "uuid";
import {addDoc, collection} from "firebase/firestore";

const JweetFactory = ({userObj }) => {
    const [jweet,setJweet] = useState("");
    //    +       "" 
    const [attachment,setAttachment] = useState("");

    const onSubmit = async (event) => {
        // document
        event.preventDefault();

        let attachmentUrl = "";

        //      attachment   
        if(attachment !== ""){
            //    
            const fileRef = ref(storageService, `${userObj.uid}/${v4()}`);

            const response = await uploadString(fileRef, attachment, "data_url");
            attachmentUrl = await getDownloadURL(response.ref);
        }

        const jweetObj = {
            text: jweet,
            createdAt: Date.now(),
            creatorId: userObj.uid,
            attachmentUrl
        }

        //  jweetObj  document  jweets  
        await addDoc(collection(dbService, "jweets"), jweetObj);


        //state  form 
        setJweet("");

        //  img src 
        setAttachment("");

        /*
        try {
            const docRef = await addDoc(collection(dbService, "jweets"), {
                text: jweet,
                createdAt: Date.now(),
                creatorId: userObj.uid
            });
            console.log(docRef);
        }catch (error) {
            console.error("Error adding document: ", error);
        }
        setJweet("");
         */
    }
    const onChange = (event) => {
        const {
            target: { value },
        } = event;
        setJweet(value);
    };

    const onFileChange = (event) =>{
        // onchange  files 
        const { target : {files}} = event;
        const picFile = files[0]; //    
        const fileReader = new FileReader();
        fileReader.onloadend = (finishedEvent) => {
            const { currentTarget : {result} } = finishedEvent;
            setAttachment(result);
        }
        //    
        fileReader.readAsDataURL(picFile);
    }

    const onClearAttachment = (event) =>  setAttachment(null);

    return(
        <form onSubmit={onSubmit}>
            <input
                type="text"
                placeholder="   ?"
                maxLength={120}
                value={jweet}
                onChange={onChange}
            />
            <input type="file" accept="image/* " onChange={onFileChange}/>
            <input type="submit" value="Jweet :)"/>
            {attachment && (
                <div>
                    <img src={attachment} width="50px"/>
                    <button onClick={onClearAttachment}> </button>
                </div>
            )}

        </form>
    )
}

export default JweetFactory;